













































.hot-container {
	width: 1226px;
	height: auto;
	margin: 0 auto;
}

.hot-header {
	position: relative;
}

.hot-header .title {
	margin: 0;
	font-size: 22px;
	font-weight: 200;
	line-height: 58px;
	color: #333;
}

.hot-wrap {
	position: relative;
}

.hot-content .hot-item {
	float: left;
	width: 296px;
	height: 415px;
	margin: 0 0 14px 14px;
	cursor: pointer;
	transition: all 0.3s;
	background: #fff;
}

.hot-content .hot-item .item-image {
	width: 296px;
	height: 220px;
}

.hot-content .hot-item .review-txt {
	height: 72px;
	margin: 22px 28px;
	font-size: 14px;
	line-height: 24px;
	font-weight: 400;
	overflow: hidden;
}

.hot-content .hot-item .author {
	position: relative;
	height: 18px;
	margin: 0 28px 8px;
	padding: 0 10px 0 0;
	font-size: 12px;
	color: #b0b0b0;
}

.hot-content .hot-item:nth-child(1) {
	margin-left: 0;
}

.hot-content .hot-item:hover {
	-webkit-transform: translateY(-3px);
	        transform: translateY(-3px);
	box-shadow: 5px 5px 20px #ccc;
}

.item-desc {
	padding-left: 30px;
}

.item-desc .item-name {
	font-size: 14px;
	color: #333;
}

.item-desc .sep {
	margin: 0 4px;
	color: #e0e0e0;
}

.item-desc .item-price {
	font-size: 14px;
	color: #ff6700;
}




