html {
    width: 100%;
    height: 100%
}
body {
    font-family: Helvetica, Arial, "Microsoft Yahei", "Hiragino Sans GB", "Heiti SC", "WenQuanYi Micro Hei", sans-serif;
    width: 100%;
    height: 100%
}
ul {
    margin: 0;
    padding: 0;
    list-style: none
}
.clearfix:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden
}
.clearfix {
    +height: 1%
}
a {
    text-decoration: none;
    color: #333
}



