


































































































.content {
	position: relative;
	float: left;
	width: 296px;
	height: auto;
	margin: 0 0 14px 14px;
	padding-top: 45px;
	background: #fff;
	transition: all 0.3s;
}
.content .title {
	margin: 0 10px 18px;
	font-size: 16px;
	font-weight: 400;
	text-align: center;
}
.content:hover {
	-webkit-transform: translateY(-3px);
	        transform: translateY(-3px);
	box-shadow: 5px 5px 20px #ccc;
}
.content:hover .slide-pre, .content:hover .slide-next {
	opacity: 1;
}
.content:nth-child(1) {
	margin-left: 0;
}
.book {
	border-top: 1px solid #ffac13;
}
.book .title {
	color: #ffac13;
}
.book .btn-txt {
	color: #ffac13;
	border: 1px solid #ffac13;
	background: #fff
}
.book .btn-txt:hover {
	color: #fff;
	background: #ffac13;
}
.book .first-desc {
	margin-bottom: 60px;
}

.theme {
	border-top: 1px solid #83c44e;
}

.theme .title {
	color: #83c44e;
}

.theme .btn-txt {
	color: #83c44e;
	border: 1px solid #83c44e;
	background: #fff
}

.theme .btn-txt:hover {
	color: #fff;
	background: #83c44e;
}

.game {
	border-top: 1px solid #e53935;
}

.game .title {
	color: #e53935;
}

.game .btn-txt {
	color: #e53935;
	border: 1px solid #e53935;
	background: #fff
}

.game .btn-txt:hover {
	color: #fff;
	background: #e53935;
}

.app {
	border-top: 1px solid #2196f3;
}

.app .title {
	color: #2196f3;
}

.app .btn-txt {
	color: #2196f3;
	border: 1px solid #2196f3;
	background: #fff
}

.app .btn-txt:hover {
	color: #fff;
	background: #2196f3;
}
.slide-wrap {
	width: 296px;
	height: 340px;
	overflow: hidden;
}
.slide-wrap .sub-content {
	width: 1200px;
	height: 340px;
	transition: all 0.5s ease;
}
.slide-wrap .sub-content .content-item {
	position: relative;
	float: left;
	width: 296px;
	height: 340px;
}
.slide-wrap .slide-pre, .slide-wrap .slide-next {
	display: block;
	width: 20px;
	height: 46px;
	line-height: 46px;
	font-size: 15px;
	text-align: center;
	margin-top: -24px;
	cursor: pointer;
	z-index: 11;
	background: #b0b0b0;
	color: #fff;
}
.slide-wrap .slide-pre {
	position: absolute;
	left: 0;
	top: 50%;
	opacity: 0;
	transition: all 0.3s;
}
.slide-wrap .slide-next {
	position: absolute;
	right: 0;
	top: 50%;
	opacity: 0;
	transition: all 0.3s;
}

.subTitle {
	margin: 0 0 10px 0;
	font-weight: normal;
	font-size: 20px;
	text-align: center;
	color: #333;
}
.desc {
	font-size: 12px;
	line-height: 1.5;
	text-align: center;
	color: #b0b0b0;
	width: 200px;
	margin: 0 auto 10px;
}
.content-image {
	display: block;
	width: 216px;
	height: 154px;
	margin: 0 auto;
}
.price {
	font-size: 14px;
	text-align: center;
	color: #333;
}
.btn-txt {
	width: 130px;
	height: 30px;
	line-height: 30px;
	font-size: 12px;
	text-align: center;
	margin: 0 auto;
	margin-bottom: 14px;
	background: #fff;
	cursor: pointer;
}

.dot-list {
	display: block;
	position: absolute;
	left: 50%;
	bottom: 10px;
	width: 140px;
	margin-left: -70px;
	z-index: 11;
}

.dot-list .dot-item {
	display: inline-block;
	width: 10px;
	height: 10px;
	padding: 10px;
	margin: 0 2px;
	cursor: pointer;
}

.dot-list .dot-item .dot {
	display: block;
	width: 6px;
	height: 6px;
	border: 2px solid #f5f5f5;
	border-radius: 6px;
	text-align: left;
	text-indent: -9999em;
	overflow: hidden;
	background-color: #b0b0b0;
	transition: all .5s;
}

.dot-list .dot-item.active .dot {
	background: #fff;
	border: 2px solid #ff6700;
}



