























































































.recommend-container {
	width: 1226px;
	height: auto;
	margin: 0 auto;
}

.recommend-header {
	position: relative;
}

.recommend-header .title {
	margin: 0;
	font-size: 22px;
	font-weight: 200;
	line-height: 58px;
	color: #333;
}

.recommend-header .control-part {
	position: absolute;
	top: 10px;
	right: 0;
	float: left;
	display: block;
}

.recommend-header .control-part .control {
	display: block;
	float: left;
	width: 34px;
	height: 23px;
	line-height: 23px;
	text-align: center;
	border: 1px solid #e0e0e0;
	cursor: pointer;
}

.recommend-header .control-part .control .icon-arrow {
	color: #b0b0b0;
	font-size: 10px;
}

.recommend-header .control-part .control:hover .icon-arrow {
	color: #ff6700;
}

.recommend-header .control-part .control.disable .icon-arrow {
	color: #e0e0e0;
}

.recommend-header .control-part .control.disable:hover .icon-arrow {
	color: #e0e0e0;
}
.recommend-wrap {
	width: 1226px;
	height: 310px;
	overflow: hidden;
}
.recommend-content {
	width: auto;
	height: 300px;
	transition: all 0.3s;
}
.recommend-content .recomend-item {
	float: left;
	display: block;
	width: 234px;
	height: 300px;
	margin: 0 13px 14px 0;
	font-size: 14px;
	background: #fff;
	cursor: pointer;
	transition: all 0.3s;
}
.recommend-content .recomend-item .item-image {
	display: block;
	width: 140px;
	height: 140px;
	margin: 40px auto 15px;
}
.recommend-content .recomend-item .item-name {
	margin-bottom: 10px;
	font-weight: normal;
	font-size: 14px;
	text-align: center;
	color: #333;
}
.recommend-content .recomend-item .item-price {
	margin-bottom: 10px;
	font-size: 14px;
	text-align: center;
	color: #ff6700;
}
.recommend-content .recomend-item .item-favorable {
	font-size: 14px;
	text-align: center;
	color: #757575;
}
.recommend-content .recomend-item:hover {
	-webkit-transform: translateY(-3px);
	        transform: translateY(-3px);
}
.recommend-content .recomend-item:nth-last-child(5n+1) {
	margin-right: 0;
}




