
















































































.video-container {
	width: 1226px;
	height: auto;
	margin: 0 auto;
}
.header {
	position: relative;
}
.header .title {
	display: block;
	float: left;
	margin: 0;
	font-size: 22px;
	font-weight: 200;
	line-height: 58px;
	color: #333;
}
.header .top-sub {
	float: right;
	display: block;
	font-size: 16px;
	line-height: 58px;
	color: #424242;
	cursor: pointer;
}
.header .top-sub .sub-icon {
	color: #b0b0b0;
	font-size: 20px;
}
.header .top-sub:hover {
	color: #ff6700;
}
.header .top-sub:hover .sub-icon {
	color: #ff6700;
}

.video-wrap .video-item {
	position: relative;
	float: left;
	width: 296px;
	height: 285px;
	margin: 0 0 14px 14px;
	background: #fff;
	cursor: pointer;
	transition: all 0.3s;
}

.video-wrap .video-item img {
	width: 296px;
	height: 180px;
}

.video-wrap .video-item .video-title {
	color: #333;
	font-size: 14px;
	font-weight: normal;
	text-align: center;
}

.video-wrap .video-item .video-desc {
	height: 18px;
	margin: 0;
	font-size: 12px;
	text-align: center;
	color: #b0b0b0;
}

.video-wrap .video-item:hover {
	-webkit-transform: translateY(-3px);
	        transform: translateY(-3px);
	box-shadow: 5px 5px 20px #ccc;
}

.video-wrap .video-item:hover .play {
	background: #ff6700;
}

.video-wrap .video-item:nth-child(1) {
	margin-left: 0;
}

.item-top {
	position: relative;
}

.item-top .play {
	position: absolute;
	display: block;
	left: 20px;
	bottom: 10px;
	width: 32px;
	height: 20px;
	line-height: 20px;
	color: #fff;
	text-align: center;
	font-size: 12px;
	border: 2px solid #fff;
	border-radius: 12px;
	background: #333;
}



