









































































































































































































.footer {
	width: 1226px;
	height: auto;
	margin: 0 auto;
	padding-bottom: 20px;
}

.footer-service {
	padding: 27px 0;
	border-bottom: 1px solid #e0e0e0;
}
.service .service-item {
	float: left;
	width: 245px;
	height: 27px;
	line-height: 27px;
	font-size: 16px;
	color: #616161;
	padding: 0 40px;
	box-sizing: border-box;
	border-right: 1px solid #e0e0e0;
}
.service .service-item .icon {
	display: inline-block;
	height: 27px;
	line-height: 27px;
	font-size: 24px;
	margin-right: 10px;
}
.service .service-item:nth-last-child(1) {
	border-right: 0;
}
.footer-info {
	padding: 40px 0;
}

.footer-content .footer-item {
	float: left;
	width: 160px;
	height: 120px;
}

.footer-content .footer-item a {
	text-decoration: none;
	color: #333;
}

.footer-content .footer-item h3 {
	font-size: 14px;
	font-weight: normal;
	margin-bottom: 26px;
}

.footer-content .footer-item p {
	font-size: 12px;
	margin-top: 10px;
}

.footer-content .footer-item:nth-last-child(2) {
	padding-right: 20px;
	border-right: 1px solid #e0e0e0;
}

.footer-content .footer-last-item {
	float: left;
}

.footer-content .footer-last-item .col-content {
	margin-left: 40px;
}

.footer-content .footer-last-item .col-content .phone-number {
	margin: 0 0 5px;
	font-size: 22px;
	text-align: center;
	margin-bottom: 5px;
	color: #ff6700;
}

.footer-content .footer-last-item .col-content .phone-desc {
	font-size: 12px;
	line-height: 1.5;
	text-align: center;
	color: #616161;
}

.footer-content .footer-last-item .col-content .server-btn {
	display: block;
	width: 120px;
	height: 30px;
	line-height: 30px;
	font-size: 12px;
	text-align: center;
	margin: 0 auto;
	border: 1px solid #ff6700;
	color: #ff6700;
	cursor: pointer
}

.footer-content .footer-last-item .col-content .server-btn:hover {
	background: #ff6700;
	color: #fff;
}

.footer-explan .logo-img {
	float: left;
	width: 57px;
	height: 57px;
}

.footer-explan .explan-content {
	float: left;
	padding-left: 10px;
}

.footer-explan .explan-content .links .link-item {
	float: left;
	padding: 0 5px;
	font-size: 12px;
	color: #757575;
	border-right: 1px solid #757575
}

.footer-explan .explan-content .links .link-item:nth-last-child(1) {
	border-right: 0;
}

.footer-explan .explan-txt {
	margin: 10px 0 0 5px;
	line-height: 18px;
	font-size: 12px;
	color: #b0b0b0;
}

.footer-explan .explan-txt a {
	text-decoration: none;
	color: #b0b0b0
}

.footer-explan .explan-txt a:hover {
	color: #ff6700;
}

.footer-explan .logo-explan {
	width: 85px;
	height: 28px
}

.footer-explan .logo-explan.logo-explan-first {
	margin-left: 50px;
}

.site-info {
	padding: 30px 0;
}

.site-info .footer-desc {
	text-align: left;
	text-indent: -9999em;
	overflow: hidden;
	width: 267px;
	height: 19px;
	margin: 30px auto 0;
	background: url('http://s01.mifile.cn/i/slogan2016.png') center center no-repeat;
}



