






















.goods-left {
	float: left;
  width: 234px;
  height: auto;
}
.goods-left .goods-left-item {
	width: 234px;
	height: 300px;
	margin-bottom: 14px;
	cursor: pointer;
	transition: all 0.3s;
}
.goods-left .goods-left-item img {
	width: 234px;
	height: 300px;
}
.goods-left .goods-left-item:hover {
	-webkit-transform: translateY(-2px);
	        transform: translateY(-2px);
	box-shadow: 5px 5px 20px #ccc;
}
.goods-left .item-bottom {
	margin-top: 14px;
}



