























































































.goods-right {
	float: left;
	width: 992px;
	height: 628px;
}

.goods-right .goods-right-content {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-flow: row wrap;
	        flex-flow: row wrap;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	width: 100%;
	height: 100%;
}

.goods-right-item {
	position: relative;
	margin: 0 0 14px 14px;
	padding: 20px 0;
	width: 234px;
	height: 260px;
	background: #fff;
	cursor: pointer;
	transition: all 0.3s;
}

.goods-right-item .discount-label {
	position: absolute;
	top: 0;
	left: 50%;
	width: 64px;
	height: 20px;
	line-height: 20px;
	margin-left: -32px;
	font-size: 12px;
	text-align: center;
	color: #fff;
	z-index: 4
}

.goods-right-item .discount-label.free {
	background-color: #ffac13;
}

.goods-right-item .discount-label.new {
	background-color: #83c44e;
}

.goods-right-item .discount-label.discount {
	background-color: #e53935;
}

.goods-right-item .old-price {
	color: #b0b0b0;
	text-decoration: line-through;
}

.goods-right-item img {
	display: block;
	width: 160px;
	height: 160px;
	margin: 0 auto;
}

.goods-right-item .goods-title {
	font-size: 14px;
	text-align: center;
	color: #333;
	font-weight:normal;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

.goods-right-item .goods-price {
	color: #ff6700;
	font-size: 14px;
	text-align: center;
}

.goods-right-item .goods-rank {
	margin: 0 10px;
	font-size: 12px;
	text-align: center;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	color: #b0b0b0;
}

.goods-right-item:hover {
	-webkit-transform: translateY(-1px);
	        transform: translateY(-1px);
	box-shadow: 5px 5px 20px #ccc;
}

.goods-right-item:hover .review {
	height: 76px;
	opacity: 1;
}

.goods-right-small {
	width: 234px;
	height: 260px;
}

.goods-right-small .item-small {
	width: 234px;
	height: 143px;
	margin: 0 0 14px 14px;
	padding: 30px 0 0 30px;
	background: #fff;
	cursor: pointer;
	transition: all 0.3s;
	box-sizing: border-box;
}

.goods-right-small .item-small .desc {
	float: left;
	width: 80px;
	height: 100%;
	margin-right: 20px;
}

.goods-right-small .item-small .desc .title {
	font-size: 14px;
	text-align: center;
	color: #333;
	font-weight: normal;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

.goods-right-small .item-small .desc .price {
	display: block;
	width: 100%;
	color: #ff6700;
	font-size: 14px;
	text-align: center;
}

.goods-right-small .item-small .small-img {
	float: left;
	width: 80px;
	height: 80px;
}

.goods-right-small .item-small:hover {
	-webkit-transform: translateY(-1px);
	        transform: translateY(-1px);
	box-shadow: 5px 5px 20px #ccc;
}

.goods-right-small .icon-arrow {
	margin-top: 5px;
	color: #ff6700;
}

.review {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 234px;
	font-size: 12px;
	height: 0;
	line-height: 18px;
	overflow: hidden;
	box-sizing: border-box;
	color: #fff;
	background: #ff6700;
	opacity: 0;
	transition: all 0.3s;
}

.review .review-content {
	display: block;
	text-align: center;
	margin: 8px 30px;
}

.review .review-author {
	display: block;
	text-align: center;
	margin-bottom: 8px;
}



